.search{
    width: 300px;
    height: 100%;
    display: flex;
    align-items: center;
    input{
        min-width: 70%;
        height: 50px;
        border-radius: 20px;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
        outline: none;
        font-size: 40px;
        padding-left: 30px;
        @media(max-width: 500px){
            font-size: 30px;
        }
    }
}

